  font-style: normal;
  font-weight: 400;
  font-size: 1.4814814815dvh;
  line-height: 1.5rem;
  font-family: "GolosTextWebVF", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
