.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 5.21%;
}
@media (orientation: portrait) {
  .container {
    padding: 0 1.5rem;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 30;
  width: 100%;
  min-height: 7.25rem;
  padding: 1.875rem 0;
  background-color: #F2F2F2;
}
@media (orientation: portrait) {
  .header {
    min-height: 72px;
    padding: 20px 0;
  }
}
.header.is-open {
  background-color: #000000;
}
.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .logo {
  flex-shrink: 0;
  margin-right: 1.25rem;
}
@media (orientation: portrait) {
  .header .logo {
    margin-right: 20px;
  }
}
@media (orientation: portrait) {
  .header__nav {
    position: fixed;
    top: 72px;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    background-color: #000000;
    overflow: auto;
    padding: 0 24px 30px 24px;
  }
  .is-open .header__nav {
    opacity: 1;
    pointer-events: all;
    visibility: visible;
  }
}
.header__list {
  display: flex;
  align-items: center;
  column-gap: 1.875rem;
  row-gap: 0.625rem;
  flex-wrap: wrap;
  margin: 0;
  padding: 0.8125rem 1.9375rem;
  border-radius: 31.25rem;
  border: 0.0625rem solid rgba(150, 158, 174, 0.15);
  list-style: none;
  font-size: 0.9375rem;
  line-height: normal;
  letter-spacing: -0.15px;
}
@media (orientation: portrait) {
  .header__list {
    flex-direction: column;
    gap: 50px;
    padding: 14px 23px;
    border-radius: initial;
    border: none;
    font-size: 20px;
    line-height: 21px;
  }
}
@media (orientation: portrait) {
  .header__link {
    color: #ffffff;
  }
}
.header__burger {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 22px;
  height: 22px;
  padding: 5px 0;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.header__burger span {
  width: 22px;
  height: 2px;
  background-color: #000000;
  transition: opacity 0.3s ease, transform 0.6s ease;
}
@media (orientation: portrait) {
  .header__burger {
    display: flex;
  }
}
.is-open .header__burger span {
  background-color: #ffffff;
}
.is-open .header__burger span:first-child {
  transform: translateY(5px) rotate(225deg);
}
.is-open .header__burger span:last-child {
  transform: translateY(-5px) rotate(-225deg);
}
.header__logo-apple {
  flex-shrink: 0;
  width: 3.5rem;
  height: 3.5625rem;
  margin-left: 1.25rem;
}
@media (orientation: portrait) {
  .header__logo-apple {
    display: none;
  }
}
.header__app-store {
  display: none;
  width: 167px;
  height: 50px;
}
@media (orientation: portrait) {
  .header__app-store {
    display: block;
  }
}
.header__socials {
  display: none;
  flex-direction: column;
  align-items: center;
}
@media (orientation: portrait) {
  .header__socials {
    display: flex;
  }
}
.header__socials-text {
  margin: 0;
  margin-bottom: 10px;
  color: #7B7B7B;
  font-size: 13px;
  line-height: normal;
  letter-spacing: -0.13px;
}
.header__socials-list {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin: 0;
  padding-left: 0;
  list-style: none;
}

@media (orientation: portrait) {
  .header--shadow {
    box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.1), 0 0 0 4px rgba(255, 255, 255, 0.7);
  }
}

@media (min-width: 768px) {
  .header--last-section .header__nav {
    display: none;
  }
  .header--last-section .header__apple {
    display: none;
  }
}

.main-nav__list {
  display: flex;
  margin: 0;
  margin-right: 20px;
  margin-left: 60px;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  margin-right: 20px;
}
.main-nav__link {
  color: #ffffff;
}
.main-nav__link.is-active {
  opacity: 0.6;
}
.main-nav__toggle {
  display: none;
}

.footer {
  padding: 40px 0;
  background-color: #2c39f2;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .modal__close-btn:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.modal__close-btn.focus-visible:focus {
  opacity: 0.5;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
