  background-image: radial-gradient(circle at 47% 14%, rgba(205, 205, 205, 0.04) 0%, rgba(205, 205, 205, 0.04) 43%, transparent 43%, transparent 100%), radial-gradient(circle at 35% 12%, rgba(215, 215, 215, 0.04) 0%, rgba(215, 215, 215, 0.04) 4%, transparent 4%, transparent 100%), radial-gradient(circle at 1% 35%, rgba(24, 24, 24, 0.04) 0%, rgba(24, 24, 24, 0.04) 37%, transparent 37%, transparent 100%), radial-gradient(circle at 21% 1%, rgba(0, 0, 0, 0.04) 0%, rgba(0, 0, 0, 0.04) 26%, transparent 26%, transparent 100%), radial-gradient(circle at 23% 82%, rgba(249, 249, 249, 0.04) 0%, rgba(249, 249, 249, 0.04) 60%, transparent 60%, transparent 100%), radial-gradient(circle at 11% 54%, rgba(251, 251, 251, 0.04) 0%, rgba(251, 251, 251, 0.04) 23%, transparent 23%, transparent 100%), radial-gradient(circle at 69% 68%, rgba(234, 234, 234, 0.04) 0%, rgba(234, 234, 234, 0.04) 10%, transparent 10%, transparent 100%), linear-gradient(90deg, #8d3dae, #8d3dae);
}

.hero {
  position: relative;
  height: 100%;
  padding-top: 3.9375rem;
  /*
  display: flex;
  flex-direction: column;
  align-items: center;

  padding-top: size(63px);
  height: 100%;

  @include portrait {
    //height: calc(100dvh - 72px);
    height: 100dvh;
    padding-top: 72px;

    padding-bottom: 30px;
  }
  */
}
@media (orientation: portrait) {
  .hero {
    height: 100dvh;
    padding-top: 72px;
    padding-bottom: 35px;
    overflow: hidden;
  }
}
.hero .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}
@media (orientation: portrait) {
  .hero .container {
    position: relative;
    height: calc(100dvh - 72px - 35px);
  }
}
.hero__title {
  margin: 0;
  width: 72%;
  font-family: "GolosUIWebMedium", "Arial", sans-serif;
  text-align: center;
  font-size: 4rem;
  font-weight: 500;
  line-height: 105%; /* 67.2px */
  letter-spacing: 0.08rem;
}
@media (orientation: portrait) {
  .hero__title {
    position: relative;
    z-index: 2;
    width: 100%;
    margin-top: auto;
    margin-bottom: 3.9705882353dvh;
    font-size: 8.5333333333vw;
    line-height: 110%;
    letter-spacing: 0.02em;
  }
}
.hero__subtitle {
  margin: 0;
  margin-bottom: 0.875rem;
  text-align: center;
  font-size: 1.375rem;
  line-height: normal;
  letter-spacing: -0.01375rem;
}
@media (orientation: portrait) {
  .hero__subtitle {
    width: 60%;
    max-width: 36.7647058824dvh;
    font-size: 4vw;
    line-height: 1.2;
    letter-spacing: -0.01em;
  }
}
.hero__images {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50.375rem;
}
@media (orientation: portrait) {
  .hero__images {
    width: 128.3823529412dvh;
    height: 61.7647058824dvh;
    bottom: 22.7941176471dvh;
    z-index: -1;
  }
}
.hero__images > * {
  position: absolute;
  bottom: 0;
}
.hero__images img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.hero__model-1 {
  height: 37.9375rem;
  aspect-ratio: 358/607;
  left: 5.2%;
  transform: translateX(-20%);
}
@media (orientation: portrait) {
  .hero__model-1 {
    width: 27.3529411765dvh;
    height: auto;
  }
}
.hero__model-2 {
  height: 40.4375rem;
  aspect-ratio: 429/647;
  left: 22%;
  transform: translateX(-50%);
}
@media (orientation: portrait) {
  .hero__model-2 {
    width: 32.7941176471dvh;
    height: auto;
  }
}
.hero__model-3 {
  height: 41.875rem;
  aspect-ratio: 646/670;
  left: 35%;
  transform: translateX(-50%);
}
@media (orientation: portrait) {
  .hero__model-3 {
    width: 49.4117647059dvh;
    height: auto;
    left: 37%;
  }
}
.hero__model-4 {
  height: 50.375rem;
  aspect-ratio: 877/806;
  left: 50%;
  transform: translateX(-50%);
}
@media (orientation: portrait) {
  .hero__model-4 {
    width: 67.0588235294dvh;
    height: auto;
    left: 51.5%;
  }
}
.hero__model-5 {
  height: 40.125rem;
  aspect-ratio: 418/642;
  left: 64%;
  transform: translateX(-50%);
}
@media (orientation: portrait) {
  .hero__model-5 {
