    height: auto;
  }
}
.hero__button {
  display: none;
}
@media (orientation: portrait) {
  .hero__button {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 48.5294117647dvh;
    min-height: 7.6470588235dvh;
    border-radius: 1.7647058824dvh;
    background-color: #000000;
    padding: 2.0588235294dvh;
    border: none;
    font-family: "GolosTextWebVF", "Arial", sans-serif;
    font-weight: 400;
    font-size: 2.5dvh;
    line-height: 1.3;
    letter-spacing: -0.4px;
    color: #ffffff;
  }
  .hero__button svg {
    width: 3.5294117647dvh;
    height: 3.5294117647dvh;
    margin-right: 0.8823529412dvh;
  }
}
.hero__sublayer {
  display: none;
}
@media (orientation: portrait) {
  .hero__sublayer {
    display: block;
    position: absolute;
    bottom: 0;
    z-index: 2;
    width: 100%;
    height: 60%;
    background: linear-gradient(180deg, rgba(242, 242, 242, 0) 0%, #F2F2F2 49.42%);
  }
}

